import { graphql } from 'gatsby'
import _ from 'lodash'
import React from 'react'
import { Head } from '../components/head'
import { HOME_TITLE } from '../constants'
import { Layout } from '../layout'
import { CoverImage } from '../components/cover-image'
import DesignSystemCoverImage from '../../content/assets/Website-header_Design-systems_v1.jpg'
import '../styles/style.scss'
import '../styles/mobile-style.scss'
import './design-system.scss'
 
export default ({ data, location }) => {
  const { siteMetadata } = data.site;

  return (
    <Layout location={location} title={siteMetadata.title}>
        <Head title={HOME_TITLE} keywords={siteMetadata.keywords} />
        <CoverImage image={DesignSystemCoverImage} />
        <div className="design-system-page">
            <div className="design-system-page__intro"> 
                <h1 className="design-system-page__intro--title"><strong>Design</strong> Systems</h1>
                <p>
                    An optimal user experience and uniform brand appearance are crucial for companies bringing digital products to market. As products become more complex, teams grow, front-end technology changes at lightning speed, and consumers become more demanding, it's increasingly difficult to achieve this in practice. The slower you develop your products and the greater the number of inconsistencies in your communications, the greater the chance that customers will switch to a competitor.
                </p>
        
                <h2 className="design-system-page__intro--subTitle">Design system as <em>a single source of truth</em></h2>
                <p>
                    With our Ordina Solution for Design Systems, we help your organization to build the best digital products in the shortest possible time. We develop a design system; an online platform containing a collection of standards for design and front-end code, reusable design components and design tools with a manual for their use. In this way we create a so-called single source of truth for your organization. This ensures that your digital products come to market faster, more efficiently and more consistently. In addition, a design system is an excellent means of breaking through the silos between IT, design and business.
                </p>
                <p>
                    Benefits of the Ordina Solution for Design systems:
                </p>
                <ul>
                    <li><strong>Faster time-to-market</strong> for new products and features.</li>
                    <li>A streamlined design and development process that results in <strong>efficiency gains</strong> for product development teams and <strong>better coordination between designers and developers.</strong></li>
                    <li>A <strong>single source of truth</strong> where you can always find the most up-to-date frontend components, design libraries and UX documentation</li>
                    <li><strong>Better product quality</strong> thanks to a unified design and development language.</li>
                    <li><strong>Reduced development and design costs</strong> through the use of reusable components and clear standards.</li>
                    <li><strong>Increased user experience</strong>, which promotes customer engagement and <strong>increases conversion and retention.</strong></li>
                </ul>

                <h2 className="design-system-page__intro--subTitle">Our approach</h2>
                <p>
                    Our standard approach to the development of a design system consists of three phases: discovery, delivery and accelerate. We guide your organization from the intake to the development and adoption of the design system. Does your organization already have an existing design system? Then we can also step in in phase 2.
                </p>
                <p>
                    In all cases, we distinguish ourselves by approaching a design system not only as a product that can improve your operational results in the short term, but also by looking at the process and the governance. This ensures that the design system is always up-to-date and does what it is meant to do. Namely: bringing your digital products to market faster, more efficiently and more consistently.
                </p>
            </div>
            {/* <img src="../../content/assets/Design-Systems_aanpak_FINAL_website-1536x727.jpg"/> */}
        </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
