import React from 'react'
import './index.scss'
import Image from 'gatsby-image'

export const CoverImage = ({ image, imageFluid, title }) =>
  <div>
    {image ?
      <img className='cover-image'
           src={image}
           alt='title' />
      :
      <Image className='cover-image'
             alt={title}
             fluid={imageFluid.childImageSharp.fluid} />
    }
    <div className='cover-image-overlay' />
  </div>
